import { cancelBubble } from '@cetus/utils'
import { Flex, HStack, Skeleton, StackProps, Text, TextProps, Tooltip, VStack } from '@chakra-ui/react'
import React from 'react'

type VTextLabelBoxProps = {
  title: string | JSX.Element
  value: string | JSX.Element
  valueTooltip?: string | JSX.Element
  subValue?: string | JSX.Element
  isLoading?: boolean
  isTitleLoading?: boolean
  skeletonStyle?: VTextLabelSkeleton
  wrapStyle?: StackProps
  titleStyle?: TextProps
  valueStyle?: TextProps
  subValueStyle?: TextProps
}

export default function VTextLabelBox(props: VTextLabelBoxProps) {
  const {
    title,
    value,
    valueTooltip,
    subValue,
    isTitleLoading,
    isLoading = false,
    skeletonStyle,
    wrapStyle = {},
    titleStyle = {},
    valueStyle = {},
    subValueStyle = {}
  } = props

  return isLoading || isTitleLoading ? (
    <VTextLabelSkeleton {...skeletonStyle} {...props} />
  ) : (
    <VStack gap="3px" alignItems="start" {...wrapStyle}>
      <Text color="text_paragraph" fontSize="14px" whiteSpace="nowrap" {...titleStyle}>
        {title}
      </Text>

      <HStack>
        {valueTooltip ? (
          <Tooltip
            label={
              <Text fontSize="12px" lineHeight="20px">
                {valueTooltip}
              </Text>
            }
            aria-label="Yield"
            ml="12px"
            mr="12px"
          >
            <button onClick={e => cancelBubble(e)}>
              <Text cursor="help" textDecoration="underline dotted" textUnderlineOffset="3px" fontSize="14px" color="text_caption" {...valueStyle}>
                {value}
              </Text>
            </button>
          </Tooltip>
        ) : (
          <Text whiteSpace="nowrap" textAlign="start" wordBreak="break-all" fontSize="14px" color="text_caption" {...valueStyle}>
            {value}
          </Text>
        )}
      </HStack>

      {subValue && (
        <Text fontSize="14px" color="text_paragraph" {...subValueStyle}>
          {subValue}
        </Text>
      )}
    </VStack>
  )
}

type VTextLabelSkeleton = {
  titleW?: string
  valueW?: string
  subValueW?: string
  titleH?: string
  valueH?: string
  subValueH?: string
}

type VTextLabelSkeletonProps = VTextLabelSkeleton & VTextLabelBoxProps

function VTextLabelSkeleton(props: VTextLabelSkeletonProps) {
  const {
    titleW = '60px',
    valueW = '70px',
    value,
    subValueW = '80px',
    subValue,
    isTitleLoading = false,
    isLoading = false,
    title,
    titleH,
    valueH,
    subValueH,
    wrapStyle = {},
    titleStyle = {},
    valueStyle = {},
    subValueStyle = {}
  } = props
  return (
    <VStack gap="3px" alignItems="start" {...wrapStyle}>
      {isTitleLoading ? (
        <Flex h={titleH} align="center">
          <Skeleton w={titleW} />
        </Flex>
      ) : (
        <Text color="text_paragraph" fontSize="14px" whiteSpace="nowrap" {...titleStyle}>
          {title}
        </Text>
      )}
      {isLoading && (typeof value === 'string' || React.isValidElement(value)) ? (
        <Flex h={valueH} align="center">
          <Skeleton w={valueW} />
        </Flex>
      ) : (
        <Text whiteSpace="nowrap" textAlign="start" wordBreak="break-all" fontSize="14px" color="text_caption" {...valueStyle}>
          {value}
        </Text>
      )}
      {isLoading && subValue && typeof subValue === 'string' ? (
        <Flex h={subValueH} align="center">
          <Skeleton w={subValueW} />
        </Flex>
      ) : (
        subValue && (
          <Text fontSize="14px" color="text_paragraph" {...subValueStyle}>
            {subValue}
          </Text>
        )
      )}
    </VStack>
  )
}
